//state
const state = {
  activeCategory: 'skincare',
  categoryTabs: [{
      label: '',
      value: 'cleaning'
    },
    {
      label: '',
      value: 'makeup'
    },
    {
      label: '',
      value: 'skincare'
    },
    {
      label: '',
      value: 'fragrance'
    },
    {
      label: '',
      value: 'bodycare'
    },
    {
      label: '',
      value: 'male'
    }
  ],

}
//mutationsstate
const mutations = {
  SET_ACTIVE_CATEGORY(state, category) {
    state.activeCategory = category
  }
}
//actions
const actions = {
  updateCategory({
    commit
  }, category) {
    commit('SET_ACTIVE_CATEGORY', category)
  }
}
//getters:
const getters = {
  activeCategory: (state) => state.activeCategory,
}


export default {
  state,
  mutations,
  actions,
  getters
}